import Vue from 'vue'
import FButton from './button/index'
import FCol from './col/index'
import FRow from './row/index'
import FTag from './tag/index'
import Message from './message/index'
import FBreadcrumb from './breadcrumb/index'
import FBreadcrumbItem from './breadcrumb-item/index'
import FCard from './card/index'
import towTable from './two-dimensional-table/index';

const components = [
  FButton,
  FCol,
  FRow,
  FTag,
  FBreadcrumb,
  FBreadcrumbItem,
  FCard,
  towTable
]


const install = function(Vue) {
  if(install.installed) return
  components.map(component => Vue.component(component.name, component))
  Vue.prototype.$message = Message
}


export default {
  install,
  FButton,
  FCol,
  FRow,
  FTag,
  FBreadcrumb,
  FBreadcrumbItem,
  FCard,
  towTable
}



