<template>
  <span class="f-breadcrumb__item">
    <span
      :class="['f-breadcrumb__inner', to ? 'is-link': '']"
      ref="link"
      role="link">
      <slot></slot>
    </span>
    <i v-if="separatorClass" class="f-breadcrumb__separator" :class="separatorClass"></i>
    <span v-else class="f-breadcrumb__separator" role="presentation">{{separator}}</span>
  </span>
</template>

<script>
  export default{
    name: 'FBreadcrumbItem',
    props: {
      to: {},
      replace: Boolean
    },
    data() {
      return {
        separator: '',
        separatorClass: ''
      }
    },
    inject: ["FBreadcrumb"],

    mounted() {
      this.separator = this.FBreadcrumb.separator
      this.separatorClass = this.FBreadcrumb.separatorClass
      const link = this.$refs.link  // 获取span元素
      link.setAttribute('role', 'link')
      link.addEventListener('click', _ => {
        const {to, $router} = this
        if (!to || !$router) return
        this.replace ? $router.replace(to) : $router.push(to)
      })
    }
  }
</script>



