<template>
  <transition name="f-zoom-in-center">
    <div  class="f__tag" :class="'f__tag--'+color">
      <span class="f__tag--desc"><slot></slot></span>
      <i class="f-icon-close2 f__tag--close" v-if="closable" @click="hiddenTag"></i>
    </div>
  </transition>
</template>

<script>
  export default{
    name: 'FTag',
    props: {
      closable: Boolean,
      color: {
        type: String,
        default: 'primary'
      }
    },
    data () {
      return {
        isclosed: false
      }
    },
    methods: {
      hiddenTag () {
        this.isclosed = true
        this.$emit('close')
      }
    }
  }

</script>



